import React from "react";
import Form from "react-bootstrap/Form";
import * as emailjs from "emailjs-com";
import Main from "../layout/Main";

class ParrotContact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      email: "",
      message: "",
      messageVisible: false,
      disabled: false,
      emailSent: false,
    };
    emailjs.init("user_FyFiRCGB6MXLQimbCVHkN");
  }

  handleChange = (event) => {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    this.setState({
      disabled: true,
    });

    let templateParams = {
      from_name: this.state.name,
      email: this.state.email,
      to_name: "harshildarji2001@gmail.com",
      message: this.state.message,
    };

    emailjs
      .send(
        "service_frbj0kl",
        "template_jwow0af",
        templateParams,
        "user_FyFiRCGB6MXLQimbCVHkN"
      )
      .then((res) => {
        console.log(res);
        if (res.status) {
          this.setState({
            disabled: false,
            emailSent: true,
            messageVisible: true,
          });
        } else {
          this.setState({
            disabled: false,
            emailSent: false,
            messageVisible: true,
          });
        }

        setTimeout(() => {
          this.setState({ messageVisible: false });
        }, 5000);
      })
      .catch((err) => {
        console.log(err);

        this.setState({
          disabled: false,
          emailSent: false,
          messageVisible: true,
        });

        setTimeout(() => {
          this.setState({ messageVisible: false });
        }, 5000);
      });
  };

  render() {
    return (
      <Main title="Contact" description="Contact Me">
        <article className="post">
          <header>
            <div className="title">
              <h2 data-testid="heading">Contact Me</h2>
              <p>You can directly message me from here</p>
            </div>
          </header>
          <Form onSubmit={this.handleSubmit}>
            <Form.Group>
              <Form.Label htmlFor="full-name">Full Name</Form.Label>
              <Form.Control
                id="full-name"
                placeholder="Your name"
                name="name"
                type="text"
                value={this.state.name}
                onChange={this.handleChange}
                required
              />
            </Form.Group>

            <Form.Group>
              <Form.Label htmlFor="email">Email</Form.Label>
              <Form.Control
                id="email"
                placeholder="Your email"
                name="email"
                type="email"
                value={this.state.email}
                onChange={this.handleChange}
                required
              />
            </Form.Group>

            <Form.Group>
              <Form.Label htmlFor="message">Message</Form.Label>
              <Form.Control
                id="message"
                placeholder="Want to meet up for coffee?"
                name="message"
                as="textarea"
                rows="3"
                value={this.state.message}
                onChange={this.handleChange}
                required
              />
            </Form.Group>

            <div style={{ textAlign: "center" }}>
              <button className="button" disabled={this.state.disabled}>
                Send
              </button>
            </div>

            {this.state.emailSent && this.state.messageVisible && (
              <p className="d-inline success-msg">Email Sent</p>
            )}
            {!this.state.emailSent && this.state.messageVisible && (
              <p className="d-inline err-msg">Email Not Sent</p>
            )}
          </Form>
        </article>
      </Main>
    );
  }
}

export default ParrotContact;
