import React from "react";
import { Helmet, HelmetProvider } from 'react-helmet-async';
import ParrotNavbar from "../parrotComponents/ParrotNavbar";

const Main = (props) => (
  <HelmetProvider>
    <Helmet
      titleTemplate="%s | Harshil Darji"
      defaultTitle="Harshil Darji"
      defer={false}
    >
      {props.title && <title>{props.title}</title>}
      <meta name="description" content={props.description} />
    </Helmet>
    <div id="wrapper">
        <ParrotNavbar/>
      <div id="main">{props.children}</div>
    </div>
  </HelmetProvider>
);

export default Main;
