import React from "react";
import routes from '../data/routes';
import { Link } from 'react-router-dom';
import Hamburger from "./Template/Hamburger";

function ParrotNavbar() {
  return (
    <header id="header">
      <nav className="links">
        <ul>
          {routes
            .filter((l) => !l.index)
            .map((l) => (
              <li key={l.label}>
                <Link to={l.path}>{l.label}</Link>
              </li>
            ))}
        </ul>
      </nav>
      <Hamburger />
    </header>
  );
}

export default ParrotNavbar;